<template>
  <view class="game-container">
    <!-- 顶部状态栏 -->
    <view class="status-bar">
      <view class="connection-status">
        <text class="status-text">连接状态: {{connectionStatus}}</text>
        <view :class="['status-indicator', isConnected ? 'connected' : 'disconnected']"></view>
      </view>
      <button class="refresh-btn" size="mini" @tap="refreshRoomInfo">刷新</button>
    </view>

    <!-- 房间信息卡片 -->
    <view class="room-info-card">
      <view class="room-header">
        <text class="room-title">{{roomName || '游戏房间'}}</text>
        <view class="room-id-container" @tap="copyRoomId">
          <text class="room-id">房间号: {{roomId || '未知'}}</text>
          <text class="copy-hint">点击复制</text>
        </view>
      </view>
      
      <view class="room-stats">
        <view class="stat-item">
          <text class="stat-label">在线人数</text>
          <text class="stat-value">{{memberCount}}/{{maxMembers}}</text>
        </view>
        <view class="stat-item">
          <text class="stat-label">准备人数</text>
          <text class="stat-value">{{readyCount}}/{{memberCount}}</text>
        </view>
      </view>
    </view>

    <!-- 游戏状态 -->
    <view class="game-status">
      <view class="status-item">
        <text class="status-label">游戏状态:</text>
        <text :class="['status-value', gameStarted ? 'started' : 'waiting']">{{gameStarted ? '进行中' : '等待中'}}</text>
      </view>
      <view class="status-item" v-if="!gameStarted">
        <text class="status-label">我的状态:</text>
        <text :class="['status-value', isReady ? 'ready' : 'not-ready']">{{isReady ? '已准备' : '未准备'}}</text>
      </view>
    </view>

    <!-- 玩家列表 -->
    <view class="players-container" v-if="!gameStarted">
      <view class="players-header">
        <text class="players-title">房间玩家 ({{memberCount}}/{{maxMembers}})</text>
      </view>
      <view class="players-list">
        <view class="player-item" v-for="item in playerList" :key="item.userId">
          <view class="player-info">
            <view class="player-avatar">
              <image v-if="item.avatarUrl" class="avatar-image" :src="item.avatarUrl" mode="aspectFill"></image>
              <text v-else class="avatar-text">{{item.displayName.charAt(0)}}</text>
            </view>
            <view class="player-details">
              <text class="player-name">{{item.displayName}}</text>
              <view class="player-tags">
                <text class="tag creator-tag" v-if="item.isCreator">房主</text>
                <text :class="['tag', 'ready-tag', item.isReady ? 'ready' : 'not-ready']">{{item.isReady ? '已准备' : '未准备'}}</text>
                <text class="tag self-tag" v-if="item.userId === userId">我</text>
              </view>
            </view>
          </view>
          <view class="player-status">
            <view :class="['status-indicator', item.isReady ? 'ready' : 'not-ready']"></view>
          </view>
        </view>
        <view class="players-empty" v-if="playerList.length === 0">
          <text>暂无玩家信息</text>
        </view>
      </view>
    </view>

    <!-- 操作按钮区域 -->
    <view class="action-buttons" v-if="!gameStarted">
      <button 
        :class="['action-btn', 'ready-btn', isReady ? 'ready' : 'not-ready']" 
        @tap="toggleReady"
        :disabled="!isConnected"
      >
        {{isReady ? '取消准备' : '准备'}}
      </button>
      
      <button 
        class="action-btn start-btn" 
        @tap="startGame"
        :disabled="!isConnected || !isCreator || readyCount < memberCount"
        v-if="isCreator"
      >
        开始游戏
      </button>
      
      <button 
        class="action-btn leave-btn" 
        @tap="leaveRoom"
      >
        离开房间
      </button>
    </view>

    <!-- 游戏中的操作 -->
    <view class="game-actions" v-if="gameStarted">
      <text class="game-tip">游戏进行中...</text>
      <button class="action-btn leave-btn" @tap="leaveRoom">离开游戏</button>
    </view>

    <!-- 消息列表 -->
    <view class="message-container">
      <view class="message-header">
        <text class="message-title">房间消息</text>
      </view>
      <scroll-view class="message-list" scroll-y="true" :scroll-top="scrollTop">
        <view class="message-item" v-for="item in messages" :key="item.id">
          <view class="message-time">{{item.time}}</view>
          <view :class="['message-content', item.type]">
            {{item.content}}
          </view>
        </view>
        <view class="message-empty" v-if="messages.length === 0">
          <text>暂无消息</text>
        </view>
      </scroll-view>
    </view>

    <!-- 底部提示 -->
    <view class="bottom-tips">
      <text class="tip-text" v-if="!isConnected">请检查网络连接</text>
      <text class="tip-text" v-else-if="memberCount < 2">等待更多玩家加入...</text>
      <text class="tip-text" v-else-if="readyCount < memberCount && !gameStarted">等待所有玩家准备...</text>
      <text class="tip-text" v-else-if="isCreator && readyCount === memberCount && memberCount >= 2 && !gameStarted">所有玩家已准备，可以开始游戏！</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      connectionStatus: '未连接',
      isConnected: false,
      roomName: '',
      roomId: '',
      memberCount: 0,
      maxMembers: 8,
      readyCount: 0,
      gameStarted: false,
      isReady: false,
      isCreator: false,
      userId: '',
      playerList: [],
      messages: [],
      scrollTop: 0,
      websocket: null
    }
  },
  onLoad(options) {
    this.roomId = options.roomId
    this.initGame()
  },
  onUnload() {
    this.closeWebSocket()
  },
  methods: {
    initGame() {
      this.initWebSocket()
      this.loadRoomInfo()
    },
    initWebSocket() {
      // 初始化WebSocket连接
      try {
        const token = uni.getStorageSync('token')
        const wsUrl = `ws://your-backend-url/ws/room/${this.roomId}?token=${token}`
        
        this.websocket = uni.connectSocket({
          url: wsUrl,
          success: () => {
            console.log('WebSocket连接成功')
          }
        })

        this.websocket.onOpen(() => {
          this.isConnected = true
          this.connectionStatus = '已连接'
        })

        this.websocket.onClose(() => {
          this.isConnected = false
          this.connectionStatus = '已断开'
        })

        this.websocket.onError((error) => {
          console.error('WebSocket错误:', error)
          this.isConnected = false
          this.connectionStatus = '连接错误'
        })

        this.websocket.onMessage((res) => {
          this.handleWebSocketMessage(res.data)
        })
      } catch (error) {
        console.error('初始化WebSocket失败:', error)
      }
    },
    closeWebSocket() {
      if (this.websocket) {
        this.websocket.close()
      }
    },
    async loadRoomInfo() {
      try {
        // 加载房间信息的逻辑
      } catch (error) {
        console.error('加载房间信息失败:', error)
      }
    },
    refreshRoomInfo() {
      this.loadRoomInfo()
    },
    copyRoomId() {
      uni.setClipboardData({
        data: this.roomId,
        success: () => {
          uni.showToast({
            title: '房间号已复制',
            icon: 'success'
          })
        }
      })
    },
    toggleReady() {
      if (!this.isConnected) return
      this.isReady = !this.isReady
      // 发送准备状态到服务器
      this.sendWebSocketMessage({
        type: 'ready',
        data: { isReady: this.isReady }
      })
    },
    startGame() {
      if (!this.isConnected || !this.isCreator || this.readyCount < this.memberCount) return
      // 发送开始游戏消息到服务器
      this.sendWebSocketMessage({
        type: 'startGame'
      })
    },
    leaveRoom() {
      uni.showModal({
        title: '提示',
        content: '确定要离开房间吗？',
        success: (res) => {
          if (res.confirm) {
            this.closeWebSocket()
            uni.navigateBack()
          }
        }
      })
    },
    handleWebSocketMessage(data) {
      try {
        const message = JSON.parse(data)
        switch (message.type) {
          case 'roomInfo':
            this.updateRoomInfo(message.data)
            break
          case 'playerList':
            this.updatePlayerList(message.data)
            break
          case 'gameStart':
            this.handleGameStart(message.data)
            break
          case 'message':
            this.addMessage(message.data)
            break
          default:
            console.log('未处理的消息类型:', message.type)
        }
      } catch (error) {
        console.error('处理WebSocket消息失败:', error)
      }
    },
    sendWebSocketMessage(message) {
      if (!this.isConnected) return
      this.websocket.send({
        data: JSON.stringify(message)
      })
    },
    updateRoomInfo(data) {
      this.roomName = data.name
      this.memberCount = data.memberCount
      this.maxMembers = data.maxMembers
      this.readyCount = data.readyCount
      this.gameStarted = data.gameStarted
    },
    updatePlayerList(data) {
      this.playerList = data
      this.isCreator = data.some(player => player.userId === this.userId && player.isCreator)
    },
    handleGameStart(data) {
      this.gameStarted = true
      // 处理游戏开始逻辑
    },
    addMessage(message) {
      this.messages.push({
        ...message,
        time: new Date().toLocaleTimeString()
      })
      this.$nextTick(() => {
        // 滚动到底部
        this.scrollTop = 9999
      })
    }
  }
}
</script>

<style>
.game-container {
  min-height: 100vh;
  background-color: #f6f6f6;
  padding: 20rpx;
}

.status-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20rpx;
  background-color: #fff;
  border-radius: 12rpx;
  margin-bottom: 20rpx;
}

.connection-status {
  display: flex;
  align-items: center;
}

.status-text {
  font-size: 26rpx;
  color: #666;
  margin-right: 10rpx;
}

.status-indicator {
  width: 16rpx;
  height: 16rpx;
  border-radius: 50%;
}

.status-indicator.connected {
  background-color: #4CAF50;
}

.status-indicator.disconnected {
  background-color: #f44336;
}

.refresh-btn {
  font-size: 24rpx;
  padding: 0 20rpx;
}

.room-info-card {
  background-color: #fff;
  border-radius: 12rpx;
  padding: 20rpx;
  margin-bottom: 20rpx;
}

.room-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20rpx;
}

.room-title {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
}

.room-id-container {
  display: flex;
  align-items: center;
}

.room-id {
  font-size: 26rpx;
  color: #666;
  margin-right: 10rpx;
}

.copy-hint {
  font-size: 22rpx;
  color: #4CAF50;
}

.room-stats {
  display: flex;
  gap: 30rpx;
}

.stat-item {
  display: flex;
  align-items: center;
}

.stat-label {
  font-size: 26rpx;
  color: #666;
  margin-right: 10rpx;
}

.stat-value {
  font-size: 26rpx;
  color: #333;
  font-weight: bold;
}

.game-status {
  background-color: #fff;
  border-radius: 12rpx;
  padding: 20rpx;
  margin-bottom: 20rpx;
}

.status-item {
  display: flex;
  align-items: center;
  margin-bottom: 10rpx;
}

.status-item:last-child {
  margin-bottom: 0;
}

.status-label {
  font-size: 26rpx;
  color: #666;
  margin-right: 10rpx;
}

.status-value {
  font-size: 26rpx;
  font-weight: bold;
}

.status-value.started {
  color: #4CAF50;
}

.status-value.waiting {
  color: #FF9800;
}

.status-value.ready {
  color: #4CAF50;
}

.status-value.not-ready {
  color: #f44336;
}

.players-container {
  background-color: #fff;
  border-radius: 12rpx;
  padding: 20rpx;
  margin-bottom: 20rpx;
}

.players-header {
  margin-bottom: 20rpx;
}

.players-title {
  font-size: 28rpx;
  font-weight: bold;
  color: #333;
}

.player-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20rpx;
  background-color: #f8f8f8;
  border-radius: 12rpx;
  margin-bottom: 10rpx;
}

.player-info {
  display: flex;
  align-items: center;
}

.player-avatar {
  width: 80rpx;
  height: 80rpx;
  border-radius: 40rpx;
  margin-right: 20rpx;
  overflow: hidden;
  background-color: #eee;
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar-image {
  width: 100%;
  height: 100%;
}

.avatar-text {
  font-size: 32rpx;
  color: #666;
}

.player-details {
  display: flex;
  flex-direction: column;
}

.player-name {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 6rpx;
}

.player-tags {
  display: flex;
  gap: 10rpx;
}

.tag {
  font-size: 20rpx;
  padding: 2rpx 10rpx;
  border-radius: 6rpx;
}

.creator-tag {
  background-color: #FFF3E0;
  color: #FF9800;
}

.ready-tag {
  background-color: #E8F5E9;
  color: #4CAF50;
}

.ready-tag.not-ready {
  background-color: #FFEBEE;
  color: #f44336;
}

.self-tag {
  background-color: #E3F2FD;
  color: #2196F3;
}

.player-status {
  display: flex;
  align-items: center;
}

.players-empty {
  text-align: center;
  padding: 40rpx 0;
  color: #999;
  font-size: 26rpx;
}

.action-buttons {
  display: flex;
  gap: 20rpx;
  margin-bottom: 20rpx;
}

.action-btn {
  flex: 1;
  height: 88rpx;
  border-radius: 44rpx;
  font-size: 28rpx;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.action-btn::after {
  display: none;
}

.ready-btn {
  background-color: #4CAF50;
  color: #fff;
}

.ready-btn.ready {
  background-color: #f44336;
}

.start-btn {
  background-color: #2196F3;
  color: #fff;
}

.start-btn[disabled] {
  background-color: #BDBDBD;
  color: #fff;
}

.leave-btn {
  background-color: #f44336;
  color: #fff;
}

.game-actions {
  text-align: center;
  margin-bottom: 20rpx;
}

.game-tip {
  font-size: 28rpx;
  color: #666;
  margin-bottom: 20rpx;
  display: block;
}

.message-container {
  background-color: #fff;
  border-radius: 12rpx;
  padding: 20rpx;
  margin-bottom: 20rpx;
}

.message-header {
  margin-bottom: 20rpx;
}

.message-title {
  font-size: 28rpx;
  font-weight: bold;
  color: #333;
}

.message-list {
  height: 300rpx;
}

.message-item {
  margin-bottom: 16rpx;
}

.message-time {
  font-size: 22rpx;
  color: #999;
  margin-bottom: 4rpx;
}

.message-content {
  font-size: 26rpx;
  color: #333;
  padding: 10rpx;
  border-radius: 8rpx;
  background-color: #f8f8f8;
}

.message-content.system {
  color: #666;
  font-style: italic;
}

.message-empty {
  text-align: center;
  padding: 40rpx 0;
  color: #999;
  font-size: 26rpx;
}

.bottom-tips {
  text-align: center;
  padding: 20rpx;
}

.tip-text {
  font-size: 26rpx;
  color: #666;
}
</style> 